import BoxComponent from "../components/BoxComponent.vue";

export default defineComponent({
    name: 'TarefaComponent',
    components: {
        CronometroSection,
        BoxComponent
    },
    props: {
        tarefa: {
            type: Object as PropType<ITarefa>,
            required: true
        }
    }
});
